.parsley-errors-list {
  position: absolute;
  width: 100%;
}

.parsley-errors-list > li {
  background: #fff;
  border: 1px solid #CE2C29;
  color: #CE2C29;
  display: inline-block;
  white-space: nowrap;
  position: absolute;
  height: 47px;
  padding: 0 22px;
  font-size: 16px;
  display: flex;
  line-height: 30px;
  align-items: center;
}

.parsley-errors-list > li::before {
  border-top: 15px solid #CE2C29;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  content: "";
  display: block;
  height: 0;
  left: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  width: 0;
  z-index: 2;
  bottom: -16px;
}

@media screen and (max-width: 990px) {
  .parsley-errors-list > li::before {
    border: 0;
    border-bottom: 15px solid #CE2C29;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    top: -16px;
    bottom: auto;
  }
}

.parsley-errors-list > li::after {
  border-top: 15px solid white;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  content: "";
  display: block;
  height: 0;
  left: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  width: 0;
  bottom: -14px;
  z-index: 9999;
}

